<!--author:王超楠
	created:2018-07-13
	effect:新闻记录-->
<template>
	<div id="four">
		<div class="s">
			<div class="st">
				<p>党费支付信息管理</p>
			</div>
		</div>
		<div class="tab">
			<el-form :label-position="labelPosition" label-width="80px" :model="list" class="tabb">
				<el-form-item label="题目">
					<el-input v-model="list.title" placeholder="题目"></el-input>
				</el-form-item>
				<el-form-item label="内容">
					<el-input v-model="list.content" placeholder="内容"></el-input>
				</el-form-item>
				<el-form-item label="创建人">
					<el-input v-model="list.crtUser" placeholder="创建人"></el-input>
				</el-form-item>
				<el-form-item label="栏目">
					<el-input v-model="list.programa" placeholder="栏目"></el-input>
				</el-form-item>
				<el-form-item label="栏目名称">
					<el-input v-model="list.programaName" placeholder="栏目名称"></el-input>
				</el-form-item>
				<el-button type="primary" @click="submit">添加</el-button>
				<el-button type="danger" @click="reset">重置</el-button>
			</el-form>
		</div>
		
		<el-table :data="arr.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%">
			<el-table-column prop="title" label="题目" width="180"></el-table-column>
			<el-table-column prop="crtUser" label="创建人" width="140"></el-table-column>
			<el-table-column prop="content" label="内容"></el-table-column>
			<el-table-column prop="crtTime" label="创建时间"></el-table-column>
			<el-table-column prop="programa" label="栏目"></el-table-column>
			<el-table-column prop="programaName" label="栏目名称"></el-table-column>
			<el-table-column prop="" label="操作">
				<template slot-scope="scope">
					<el-button size="mini" @click="xiuxin(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		 <!-- 分页器 -->
        <div align="center">
              <el-pagination
                  @current-change="current_change"
                  :current-page="currentPage"
                  :page-size="pagesize"
                  layout="total, prev, pager, next, jumper"
                  :total="arr.length">
              </el-pagination>
          </div>
		
		 <!-- 弹框部分 -->
          <div v-if="flag" class="xg">
                <my-log :cdz="data" class="xga" @tianjia="tianjia"></my-log>
          </div>
	</div>
</template>

<script>
	import myLog from './log'
	import URL from '../urlConfig'
	export default {
		components:{
        myLog
    },
		data() {
			return {             
                pagesize: 3, //默认每页数据量               
                currentPage: 1,//当前页码              
                totalCount: 0, //默认数据总数
				labelPosition: 'top',//表格定位
				list: {
					title: '',//题目
					content: '',//内容
					crtUser: '',//创建人
					programa: '',//栏目
					programaName: '',//栏目名称
				},
				arr:[],//表格默认数据
				flag:false
			}
		},
		methods: {
			/*
			 * params:currentPage
			 * return: 无
			 * function:页面数量
			 * */
			current_change:function(currentPage){
                this.currentPage = currentPage;
      		},
			/*
			 * params:val
			 * return: 无
			 * function: 添加的事件
			 * 
			 * */
			tianjia(val){
				this.flag=val;
				this.$axios.post(URL.newlist, {
						page: 1,
						pageSize:5,
				}).then(res => {						
						this.arr= res.data.map.list
					})
					.catch(err => {
						console.log(err)
					})
			},
			/*
			 * params:无
			 * return: 无
			 * function: 提交按钮点击事件
			 * */
			submit() {
				var all = false;
				for(var item in this.list) {
					if(this.list[item].length == 0) {
						all = true;
					}
				}
				if(all) {
					this.$layer.alert("请填写完整信息")
				} else {
					this.$axios.post(URL.addnew, {
							title:this.list.title,
							content:this.list.content,
							crtUser:this.list.crtUser,
							programa:this.list.programa,
							programaName:this.list.programaName,
						}).then(res => {
							this.newlist()
							//console.log(res.data)
							this.$layer.alert("提交成功")
						})
						.catch(err => {
							console.log(err)
						})
				}
			},
			/*
			 * params:无
			 * return: 无
			 * function: 重置按钮
			 * */
			reset(){
					this.list.id='',
					this.list.title="",
					this.list.content= '',
					this.list.crtUser= '',
					this.list.programa='',
					this.list.programaName=''
			},
				/*
			 * params:无
			 * return: 无
			 * function:删除事件
			 * */
			del(index,row){
				this.$axios.post(URL.delnewlist, {
							ids:this.arr[index].id,
					}).then(res => {
							//console.log(res.data)
//							this.dongtai();
							this.arr.splice(index,1)
							this.$layer.alert("删除成功")
						})
						.catch(err => {
							console.log(err)
						})
			},
			xiuxin(x,y){
				//console.log(x)
				this.data=y;
				this.flag=!this.flag;
			},
			/*
			 * params:无
			 * return: 无
			 * function: 请求list数据
			 * */
			newlist(){
			console.log(this.$axios)
				this.$axios.post(URL.newlist, {
						page: 1,
						pageSize:5,
					}).then(res => {
						
						this.arr= res.data.map.list
					})
					.catch(err => {
						console.log(err)
					})
			}
		},
		
		created(){
			this.newlist()
		}
		
}
</script>

<style scoped>
	.s {
		color: #9A9A9A;
		width: 100%;
		height: 60px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #DDDDDD;
	}
	
	.st {
		margin-left: 3%;
		color: #777777;
		font-size: 20px;
		font-weight: bold;
	}
	
	.tab {
		width: 100%;
		background: white;
		height: 700px;
	}
	
	.tabb {
		width: 50%;
		margin: 0 auto;
		padding-bottom: 20px;
	}
	
	.tabb p {
		padding: 20px 0 5px;
		color: #9B9AA8;
	}
	.xg{
        height: 580px;
		width: 1100px;
       position: fixed;
       top: 0;
       left: 250px;
       background: rgb(199, 202, 204)
   }
   .xga{
       width: 500px;
        margin: 0 auto;
       
   }
</style>


