<!--author:王超楠
	created:2018-07-13
	effect:政策法规-->
<template>
	<div id="fuwu">
		<div class="s">
			<div class="st">
				<p>支部活动列表管理</p>
			</div>
		</div>
		<div class="tab">
			<el-form :label-position="labelPosition" label-width="80px" :model="list" class="tabb">
				<el-form-item label="标题">
					<el-input v-model="list.activityTitle" placeholder="请输入标题"></el-input>
				</el-form-item>
				<el-form-item label="活动时间">
					<el-input type='date' v-model="list.activityTime" placeholder="创建人"></el-input>
				</el-form-item>
				<el-form-item label="内容">
					<!--<el-input v-model="list.content" placeholder="内容"></el-input>-->
					<!--<div v-loading="imageLoading" element-loading-text="请稍等，图片上传中">
						<quill-editor ref="newEditor" :options="newOption" style="height: 200px; margin-bottom: 54px" v-model="list.activityContent" @change="editorChange">
						</quill-editor>
						<form action="" method="post" enctype="multipart/form-data" id="uploadFormMulti">
							<input style="display: none" :id="uniqueId" type="file" name="avator" multiple accept="image/jpg,image/jpeg,image/png,image/gif" @change="uploadImg('uploadFormMulti')">
							<!--style="display: none"-->
					<!--</form>
					</div>-->
					<el-input v-model="list.activityContent" type="textarea" :rows="5" resize="none">

					</el-input>
				</el-form-item>
				<el-form-item label="上传图片">
					<el-upload :action="upImg" ref="uploadImages" :headers="myHeaders" name="file" :limit="3" :on-change="handleChange" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="imgList" :on-success="success" :on-exceed="limitLayer" list-type="picture-card">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible" append-to-body>
						<img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>

				<el-form-item>
					<el-button type="primary" @click="submit">添加</el-button>
				</el-form-item>
				<!--<el-button type="danger" @click="reset">重置</el-button>-->
			</el-form>
		</div>

		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="activityTitle" label="标题" width="180"></el-table-column>
			<el-table-column prop="activityContent" label="内容" width="140"></el-table-column>
			<el-table-column prop="activityTime" label="创建时间"></el-table-column>
			<el-table-column prop="" label="图片">
				<template slot-scope="scope">
					<!--<div v-for="(item,index) in " :key="index">-->
					<img :src="scope.row.url[0]" width="100" />
					<!--</div>-->
				</template>
			</el-table-column>
			<el-table-column label="操作" width="200">
				<template slot-scope="scope">
					<el-button size="mini" @click="bj(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!-- 分页器 -->
		<div align="center">
			<el-pagination @current-change="current_change" :current-page="currentPage" :page-size="pagesize" layout="total, prev, pager, next, jumper" :total="totalCount">
			</el-pagination>
		</div>
		<!-- 弹框部分 -->
		<el-dialog :visible.sync="flag" :before-close="clearList">
			<el-form :label-position="labelPosition" label-width="80px" :model="list" class="tabb">
				<el-form-item label="标题">
					<el-input v-model="list.activityTitle" placeholder="请输入标题"></el-input>
				</el-form-item>
				<el-form-item label="活动时间">
					<el-input type='date' v-model="list.activityTime" placeholder="创建人"></el-input>
				</el-form-item>
				<el-form-item label="内容">
					<!--<el-input v-model="list.content" placeholder="内容"></el-input>-->
					<!--<div v-loading="imageLoading" element-loading-text="请稍等，图片上传中">
						<quill-editor ref="newEditor" :options="newOption" style="height: 200px; margin-bottom: 54px" v-model="list.activityContent" @change="editorChange">
						</quill-editor>
						<form action="" method="post" enctype="multipart/form-data" id="uploadFormMulti">
							<input style="display: none" :id="uniqueId" type="file" name="avator" multiple accept="image/jpg,image/jpeg,image/png,image/gif" @change="uploadImg('uploadFormMulti')">
							<!--style="display: none"-->
					<!--</form>
					</div>-->
					<el-input v-model="list.activityContent" type="textarea" :rows="5" resize="none">

					</el-input>
				</el-form-item>

				<el-form-item>
					<el-button type="primary" @click="edit">确认修改</el-button>
				</el-form-item>
				<!--<el-button type="danger" @click="reset">重置</el-button>-->
			</el-form>
		</el-dialog>
	</div>
</template>

<script>
	import fagui from './fagui'
	import URL from '../urlConfig'
	import axios from '../ajax'
	import $http from 'axios'
	import * as Cookies from 'tiny-cookie'
	let TOKEN = Cookies.get('ADMINTOKEN') || '';
	export default {
		components: {
			fagui
		},
		data() {
			return {
				flag: false,
				tableData: [], //表格当前页数据
				pagesize: 3, //默认每页数据量
				currentPage: 1, //当前页码
				totalCount: 0, //默认数据总数
				labelPosition: 'top', //表格定位
				editorOption: {}, //富文本配置
				list: {
					activityTitle: '', //题目
					activityContent: '', //内容
					activityTime: '',
					url: [],
				},
				lists: {},
				upImg: URL.uploadImage,
				imgList: [],
				dialogVisible: false,
				dialogImageUrl: '',
				myHeaders: {
					"Authorization": TOKEN
				}
			}
		},
		mounted() {
			this.lists = this.list;
			//			var vm = this;
			//			var imgHandler = async function(image) {
			//				vm.addImgRange = vm.$refs.newEditor.quill.getSelection()
			//				if(image) {
			//					var fileInput = document.getElementById(vm.uniqueId) //隐藏的file文本ID
			//					fileInput.click() //加一个触发事件
			//				}
			//			}
			//			vm.$refs.newEditor.quill.getModule("toolbar").addHandler("image", imgHandler)
		},
		methods: {
			uploadImg: async function(id) {
				var vm = this
				vm.imageLoading = true
				var formData = new FormData($('#' + id)[0])
				try {
					const url = await vm.uploadImgReq(formData) // 自定义的图片上传函数
					if(url != null && url.length > 0) {
						var value = url
						vm.addImgRange = vm.$refs.newEditor.quill.getSelection()
						value = value.indexOf('http') != -1 ? value : 'http:' + value
						vm.$refs.newEditor.quill.insertEmbed(vm.addImgRange != null ? vm.addImgRange.index : 0, 'image', value, Quill.sources.USER)
					} else {
						vm.$message.warning("图片增加失败")
					}
					document.getElementById(vm.uniqueId).value = ''
				} catch({
					message: msg
				}) {
					document.getElementById(vm.uniqueId).value = ''
					vm.$message.warning(msg)
				}
				vm.imageLoading = false
			},
			/*
			 * params:file
			 * return: 无
			 * function: 提交按钮点击事件
			 * */
			handleChange(file, fileList) {
				console.log(fileList);
				this.imgList = fileList;

				//				if(fileList.length == 2) {
				//					this.$layer.msg("最多添加两张图片")
				//				}

			},
			/*
			 * params:file
			 * return: 无
			 * function: 提交按钮点击事件
			 * */
			handlePreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			/*
			 * params:文件对象,文件列表
			 * return: 无
			 * function: 上传文件
			 * */
			handleRemove(file, fileList) {
				this.imgList = fileList;

			},
			/*
			 * params:file
			 * return: 无
			 * function: 上传图片
			 * */
			success(file, fileList) { //上传图片

				this.imgList.push({

				})
			},
			/*
			 * params:无
			 * return: 无
			 * function: 最多上传多少图片
			 * */
			limitLayer() {
				//				layer.msg('最多上传1张图片');
				this.$layer.alert('最多上传3张图片');
			},
			/*
			 * params:currentPage
			 * return: 无
			 * function:页码改变数据
			 * */
			clearList() {
				this.flag = false;
				this.list = this.lists;
			},
			/*
			 * params:currentPage
			 * return: 无
			 * function:页码改变数据
			 * */
			edit() {
				let TOKEN = Cookies.get('ADMINTOKEN') || '';
				$http.post(URL.updActivity, this.list, {
						dataType: "JSON",
						headers: {
							"Content-Type": "application/json;charsetset=UTF-8",
							"Authorization": TOKEN
						}
					})
					.then(res => {
						if(res.data.code == 200) {
							this.flag = false;
							this.$layer.msg("添加成功");
							this.getData();
							this.list = this.lists;

						}

					})
					.catch(err => {
						this.$layer.msg('服务错误，请稍后重试')
					})
			},

			/*
			 * params:currentPage
			 * return: 无
			 * function:页码改变数据
			 * */
			current_change: function(currentPage) {
				this.currentPage = currentPage;
				this.getData();
			},
			/*
			 * params: val
			 * return: 无
			 * function:编辑的方法
			 * */
			tianjia(val) {
				this.flag = val;
				this.$axios.post(URL.fagui, {
						page: 1,
						pageSize: 7,
					}).then(res => {
						//console.log(res.data)
						this.arr = res.data.map.list
					})
					.catch(err => {
						console.log(err)
					})
			},
			/*
			 * params: x,y
			 * return: 无
			 * function: 显示编辑弹框
			 * */
			bj(x, y) {
				this.flag = !this.flag;

				axios.post(URL.lookActivity, {
						id: y.id
					})
					.then(res => {
						if(res.data.code == 200) {
							this.list = res.data.flag;

						}
					})
					.catch(err => {
						this.$layer.msg('请求超时，请稍后重试');
					})
			},
			/*
			 * params: 无
			 * return: 无
			 * function: 提交按钮
			 * */
			submit() {
				var ths = this;
				console.log(this.$refs.uploadImages.uploadFiles);
				this.$refs.uploadImages.uploadFiles.forEach((item, index) => {
					ths.list.url = ths.list.url.concat(item.response.flag);
				})
				
				let TOKEN = Cookies.get('ADMINTOKEN') || '';

				$http.post(URL.addActivity, this.list, {
						dataType: "JSON",
						headers: {
							"Content-Type": "application/json;charsetset=UTF-8",
							"Authorization": TOKEN
						}
					})
					.then(res => {
						if(res.data.code == 200) {
							this.$layer.msg("添加成功");
							this.getData();
							for(var item in this.list) {
								if(typeof this.list[item] == 'string') {
									this.list[item] = '';
								} else {
									this.list[item] = [];
								}

							}
							

						}

					})
					.catch(err => {
						this.$layer.msg('服务错误，请稍后重试')
					})

			},
			/*
			 * params: 无
			 * return: 无
			 * function: 重置按钮
			 * */
			reset() {
				this.list.title = "",
					this.list.content = '',
					this.list.crtUser = '',
					this.list.articleSource = '',
					this.list.releaseTime = '',
					this.list.author = ''
			},
			/*
			 * params:index,row
			 * return: 无
			 * function:删除事件
			 * */
			del(index, row) {
				axios.post(URL.delActivity, {
						ids: row.id,
					}).then(res => {
						if(res.data.code == 200) {
							this.$layer.msg("删除成功");
							this.getData();
						}
					})
					.catch(err => {
						this.$layer.msg("请求超时，请稍后重试");
					})
			},
			/*
			 * params:无
			 * return: 无
			 * function: 请求法规列表
			 * */
			getData() {
				axios.post(URL.activities, {
						pageSize: this.pagesize,
						currentPage: this.currentPage,
					}).then(res => {

						this.tableData = res.data.flag.list;
						this.totalCount = res.data.flag.totalCount;
					})
					.catch(err => {
						this.$layer.msg("请求超时，请稍后重试");
					})
			},

		},
		/*
		 * params:无
		 * return: 无
		 * function: 进页面的处理函数
		 * */
		created() {
			this.getData();
		}
	}
</script>

<style scoped>
	.s {
		color: #9A9A9A;
		width: 100%;
		height: 60px;
		display: flex;
		align-items: center;
		border-bottom: 1px solid #DDDDDD;
	}
	
	.st {
		margin-left: 3%;
		color: #777777;
		font-size: 20px;
		font-weight: bold;
	}
	
	.tab {
		width: 100%;
		background: white;
	}
	
	.tabb {
		width: 50%;
		margin: 0 auto;
		padding-bottom: 20px;
	}
	
	.xg {
		height: 570px;
		width: 1050px;
		position: fixed;
		top: 0;
		left: 250px;
		background: rgb(199, 202, 204)
	}
	
	.xga {
		width: 500px;
		margin: 0 auto;
	}
</style>


