<!--author:王超楠
	created:2018-07-13
	effect:信用动态记录-->
<template>
	<div id="fuwu">
		<div class="s">
			<div class="st">
				<p>工资列表管理</p>
			</div>
		</div>
		<!--<div class="tab">
			<el-form :label-position="labelPosition" label-width="80px" :model="list" class="tabb">
				<el-form-item label="题目">
					<el-input v-model="list.title" placeholder="题目"></el-input>
				</el-form-item>
				<el-form-item label="内容">
					<el-input v-model="list.content" placeholder="内容"></el-input>
				</el-form-item>
				<el-form-item label="创建人">
					<el-input v-model="list.crtUser" placeholder="创建人"></el-input>
				</el-form-item>
				<el-button type="primary" @click="submit">添加</el-button>
				<el-button type="danger" @click="reset">重置</el-button>
			</el-form>
		</div>-->
		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="userName" label="姓名" width="180">
			</el-table-column>
			<el-table-column prop="workUnit" label="单位名称" width="140">
			</el-table-column>
			<el-table-column prop="duesPay" label="收入">
			</el-table-column>
			<el-table-column prop="auditState" label="审核状态">
			</el-table-column>
			<el-table-column prop="" label="操作">
				<template slot-scope="scope">
					<el-button size="mini" @click="audit(scope.$index, scope.row)">审核</el-button>
					<el-button size="mini" @click="bj(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!-- 分页器 -->
		<div align="center">
			<el-pagination @current-change="current_change" :current-page="currentPage" :page-size="pagesize" layout="total, prev, pager, next, jumper" :total="totalCount">
			</el-pagination>
		</div>
		<!-- 弹框部分 -->
		<el-dialog :visible.sync="flag" :before-close="clearData">
			<div class="tabList">
				<el-input v-model="duesPay" placeholder="请填写工资收入"></el-input>
				<el-input class="pay" type='textarea' placeholder="上传凭证，如社保缴费信息等" :rows="10"></el-input>
				<div class="annotation">注：工资一旦填好后，一年后才可更改</div>
				<el-button @click="update" class="submit" round>确认修改</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import xinyong from './xinyong'
	import URL from '../urlConfig'
	import axios from '../ajax'
	import * as Cookies from 'tiny-cookie'
	import $http from 'axios'
	export default {
		components: {
			xinyong
		},
		data() {
			return {
				labelPosition: 'top', //表格定位
				flag: false,
				tableData: [], //表格当前页数据                
				pagesize: 3, //默认每页数据量               
				currentPage: 1, //当前页码              
				totalCount: 0, //默认数据总数
				list: {
					title: '', //题目
					content: '', //内容
					crtUser: '', //创建人
				},
				duesPay: '',
				id: '',
			}
		},
		methods: {
			/*
			 * params:index,row
			 *
			 * return: 无
			 * function:删除事件
			 * */
			audit(index, row) {
				const c = confirm('通过审核');
				if(c) {
					axios.post(URL.updAuditState, {
							ids: row.id,
							auditState: 1
						}).then(res => {

							if(res.data.code == 200) {
								this.$layer.msg("审核通过");
								this.getData();
							}
						})
						.catch(err => {
							this.$layer.msg('请求超时，请稍后重试');
						})
				}

			},
			/*
			 * params:currentPage
			 * return: 无
			 * function:页码数量
			 * */
			clearData() {
				this.flag = false;
				this.duesPay = '';
			},
			/*
			 * params:currentPage
			 * return: 无
			 * function:页码数量
			 * */
			update() {

				axios.post(URL.updDuesPay, {
						id: this.id,
						duesPay: this.duesPay
					})
					.then(res => {
						if(res.data.code == 200) {
							this.$layer.msg('工资修改成功');
							this.flag = false;
						} else {
							this.$layer.msg(res.data.msg);
						}
					})
					.catch(err => {
						this.$layer.msg('请求失败，请稍后');
					})
			},
			/*
			 * params:currentPage
			 * return: 无
			 * function:页码数量
			 * */
			current_change: function(currentPage) {
				this.currentPage = currentPage;
			},
			/*
			 * params: val
			 * return: 无
			 * function: 添加的事件
			 * */
			tianjia(val) {
				this.flag = val;
				//				this.$axios.post(URL.dongtai, {
				//						page: 1,
				//						pageSize: 20,
				//				}).then(res => {
				//						console.log(res.data)
				//						this.tabdata= res.data.map.list
				//					})
				//					.catch(err => {
				//						console.log(err)
				//					})
			},
			/*
			 * params:index,row
			 *
			 * return: 无
			 * function:删除事件
			 * */
			del(index, row) {
				axios.post(URL.delDuesPay, {
						ids: row.id,
					}).then(res => {

						if(res.data.code == 200) {
							this.$layer.msg("删除成功");
							this.getData();
						}
					})
					.catch(err => {
						this.$layer.msg('网络错误，请稍后重试');
					})
			},
			/*
			 * params: x,y
			 * return: 无
			 * function: 显示编辑弹框
			 * */
			bj(x, y) {
				this.id = y.id;
				this.flag = !this.flag;
				axios.post(URL.singleDuesPay, {
						id: y.id
					})
					.then(res => {
						if(res.data.code == 200) {
							this.duesPay = res.data.flag.duesPay;
						}
					})
					.catch(err => {
						this.$layer.msg('请求超时，请稍后重试');
					})

			},
			/*
			 * params:无
			 * return: 无
			 * function:提交按钮事件
			 * */
			submit() {
				var all = false;
				for(var item in this.list) {
					if(this.list[item].length == 0) {
						all = true;
					}
				}
				if(all) {
					this.$layer.alert("请填写完整信息")
				} else {
					this.$axios.post(URL.addxin, {
							title: this.list.title,
							content: this.list.content,
							crtUser: this.list.crtUser,
						}).then(res => {
							this.dongtai();
							this.$layer.alert("提交成功")
						})
						.catch(err => {
							console.log(err)
						})
				}
			},
			/*
			 * params:无
			 * return: 无
			 * function:重置按钮事件
			 * */
			reset() {
				this.list.title = "",
					this.list.content = '',
					this.list.crtUser = ''
			},
			/*
			 * params:page:  pageSize:
			 * return: 无
			 * function:获取动态新闻的列表
			 * */
			getData() {
				axios.post(URL.allDuesPay, {
						pageSize: this.pagesize,
						currentPage: this.currentPage,
					}).then(res => {
						//console.log(res.data)
						this.tableData = res.data.flag.list;
						this.totalCount = res.data.flag.totalCount;
						this.tableData.forEach((item, index) => {
							if(item.auditState) {
								item.auditState = '审核通过'
							} else {
								item.auditState = '审核未通过'
							}
						})
					})
					.catch(err => {
						console.log(err)
					})
			},
		},
		created() {
			this.getData();
		}
	}
</script>

<style scoped>
	.s {
		color: #9A9A9A;
		width: 100%;
		height: 60px;
		display: flex;
		align-items: center;
		border-bottom: 1px solid #DDDDDD;
	}
	
	.xg {
		height: 580px;
		width: 1000px;
		position: fixed;
		top: 0;
		left: 250px;
		background: rgb(199, 202, 204);
	}
	
	.xga {
		width: 500px;
		margin: 0 auto;
	}
	
	.st {
		margin-left: 3%;
		color: #777777;
		font-size: 20px;
		font-weight: bold;
	}
	
	.tab {
		width: 100%;
		background: white;
	}
	
	.tabb {
		width: 50%;
		margin: 0 auto;
		padding-bottom: 20px;
	}
</style>


