<!--author:王超楠
	created:2018-07-13
	effect:信用动态记录-->
<template>
	<div id="fuwu">
		<div class="s">
			<div class="st">
				<p>组织关系转换</p>
			</div>
		</div>
		<!--<div class="tab">
			<el-form :label-position="labelPosition" label-width="80px" :model="list" class="tabb">
				<el-form-item label="题目">
					<el-input v-model="list.title" placeholder="题目"></el-input>
				</el-form-item>
				<el-form-item label="内容">
					<el-input v-model="list.content" placeholder="内容"></el-input>
				</el-form-item>
				<el-form-item label="创建人">
					<el-input v-model="list.crtUser" placeholder="创建人"></el-input>
				</el-form-item>
				<el-button type="primary" @click="submit">添加</el-button>
				<el-button type="danger" @click="reset">重置</el-button>
			</el-form>
		</div>-->
		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="organizationName" label="转入组织部名称" width="180">
			</el-table-column>
			<el-table-column prop="userName" label="姓名" width="140">
			</el-table-column>
			<el-table-column prop="userAge" label="性別">
			</el-table-column>
			<el-table-column prop="userSex" label="年龄">
			</el-table-column>
			<el-table-column prop="userNation" label="民族">
			</el-table-column>
			<el-table-column prop="userCategory" label="是否为正式党员">
			</el-table-column>
			<el-table-column prop="userIdNumber" label="身份证号">
			</el-table-column>
			<el-table-column prop="userPhone" label="联系电话">
			</el-table-column>
			<el-table-column prop="whetherSucceed" label="审核状态">
			</el-table-column>
			<el-table-column prop="" label="操作" width="250">
				<template slot-scope="scope">
					<el-button size="mini" @click="audit(scope.$index, scope.row)">审核</el-button>
					<el-button size="mini" @click="bj(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!-- 分页器 -->
		<div align="center">
			<el-pagination @current-change="current_change" :current-page="currentPage" :page-size="pagesize" layout="total, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
		<!-- 弹框部分 -->
		<el-dialog :visible.sync="flag">
			<!--<xinyong :cdz="data" class="xga" @tianjia="tianjia"></xinyong>-->
			<el-form :model="baseForm" class="main">
				<el-form-item label="转入组织部名称：">
					<el-input v-model="baseForm.organizationName" class="left" placeholder="请点击填写"></el-input>
				</el-form-item>
				<div class="clear">
					<el-form-item class="left" label="姓名:">
						<el-input v-model="baseForm.userName" id="nameLeft" placeholder="王某某"></el-input>
					</el-form-item>
					<el-form-item class="right" label="性別：">
						<el-select v-model="baseForm.userSex">
							<el-option label="男" value="男"></el-option>
							<el-option label="女" value="女"></el-option>
						</el-select>
					</el-form-item>
				</div>
				<el-form-item label="年龄：">
					<el-input v-model="baseForm.userAge" placeholder="请点击填写"></el-input>
				</el-form-item>
				<el-form-item label="民族：">
					<el-select v-model='baseForm.userNation' placeholder="点击请选择" class="national">
						<el-option v-for="(item,index) in nations" :key="item.dicId" :label="item.dicName" :value="item.dicId"></el-option>

					</el-select>
				</el-form-item>
				<el-form-item label="是否为正式党员：">
					<el-select v-model="baseForm.userCategory">
						<el-option label="是" value="是"></el-option>
						<el-option label="否" value="否"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="身份证号：">
					<el-input v-model="baseForm.userIdNumber" placeholder="32176472341238747"></el-input>
				</el-form-item>
				<el-form-item label="联系电话：">
					<el-input v-model="baseForm.userPhone" placeholder="点击填写"></el-input>
				</el-form-item>
				<el-button type="primary" @click="submit">确认修改</el-button>
			</el-form>

		</el-dialog>
	</div>
</template>

<script>
	import xinyong from './xinyong'
	import URL from '../urlConfig'
	import axios from '../ajax'
	import $http from 'axios'
	import * as Cookies from 'tiny-cookie'
	export default {
		components: {
			xinyong
		},
		data() {
			return {
				labelPosition: 'top', //表格定位
				flag: false,

				tableData: [],
				pagesize: 3, //默认每页数据量               
				currentPage: 1, //当前页码              
				totalCount: 0, //默认数据总数
				list: {
					title: '', //题目
					content: '', //内容
					crtUser: '', //创建人
				},
				total: 0,
				nations: [],
				baseForm: {
					organizationName: '',
					userName: '',
					userAge: '',
					userSex: '',
					userNation: '',
					userCategory: '',
					userIdNumber: '',
					userPhone: ''
				},
			}
		},
		mounted() {
			this.getData();
			this.getNation();
		},
		methods: {
			/*
			 * params:index,row
			 *
			 * return: 无
			 * function:删除事件
			 * */
			getNation() {
				axios.post(URL.getNation)
					.then(res => {
						if(res.data.code == 200) {
							this.nations = res.data.list
						}

					})
					.catch(err => {
						this.$layer.msg('请求超时，请重试');
					})
			},
			/*
			 * params:index,row
			 *
			 * return: 无
			 * function:删除事件
			 * */
			audit(index, row) {
				const c = confirm('通过审核');
				if(c) {
					axios.post(URL.audits, {
							ids: row.id,
							whetherSucceed: 1
						}).then(res => {

							if(res.data.code == 200) {
								this.$layer.msg("审核通过");
								this.getData();
							}
						})
						.catch(err => {
							this.$layer.msg('请求超时，请稍后重试');
						})
				}

			},
			/*
			 * params:currentPage
			 * return: 无
			 * function:页码数量
			 * */
			getData() {

				axios.post(URL.getOrganization, {
						currentPage: this.currentPage,
						pageSize: this.pagesize,

					})
					.then((res) => {
						this.loading = false;
						//						console.log(res);
						this.tableData = res.data.flag.list;
						this.total = res.data.flag.totalCount;
						this.tableData.forEach((item, index) => {
							if(item.whetherSucceed) {
								item.whetherSucceed = '审核通过'
							} else {
								item.whetherSucceed = '审核未通过'
							}
						})
					})
					.catch((err) => {
						this.$layer.msg('请求超时，请刷新重试');
					})

				// this.setHeight();
			},

			/*
			 * params:currentPage
			 * return: 无
			 * function:页码数量
			 * */
			current_change: function(currentPage) {
				this.currentPage = currentPage;
				this.getData();
			},
			/*
			 * params: val
			 * return: 无
			 * function: 添加的事件
			 * */
			tianjia(val) {
				this.flag = val;
				//				this.$axios.post(URL.dongtai, {
				//						page: 1,
				//						pageSize: 20,
				//				}).then(res => {
				//						console.log(res.data)
				//						this.tabdata= res.data.map.list
				//					})
				//					.catch(err => {
				//						console.log(err)
				//					})
			},
			/*
			 * params:index,row
			 *
			 * return: 无
			 * function:删除事件
			 * */
			del(index, row) {

				axios.post(URL.delOrganization, {
						ids: this.tableData[index].id,
					}).then(res => {

						this.$layer.msg("删除成功");
						this.getData();
					})
					.catch(err => {
						console.log(err)
					})
			},
			/*
			 * params: x,y
			 * return: 无
			 * function: 显示编辑弹框
			 * */
			bj(x, y) {

				this.data = y;
				this.flag = !this.flag;
				axios.post(URL.getSingleOrganization, {
						id: y.id
					})
					.then(res => {
						if(res.data.code == 200) {
							this.baseForm = res.data.flag;
						}
					})
					.catch(err => {
						this.$layer.msg('请求超时，请稍后重试');
					})
				//				this.dongtai()
			},
			/*
			 * params:无
			 * return: 无
			 * function:提交按钮事件
			 * */
			submit() {
				//				var all = false;
				//				for(var item in this.list) {
				//					if(this.list[item].length == 0) {
				//						all = true;
				//					}
				//				}
				//				if(all) {
				//					this.$layer.alert("请填写完整信息")
				//				} else {
				let TOKEN = Cookies.get('ADMINTOKEN') || '';
				$http.post(URL.updOrganization, this.baseForm, {
						dataType: "JSON",
						headers: {
							"Content-Type": "application/json;charsetset=UTF-8",
							"Authorization": TOKEN
						}
					}).then(res => {
						if(res.data.code==200){
							this.flag=false;
							this.$layer.msg('修改成功');
							this.getData();
						}
					})
					.catch(err => {
						this.$layer.msg('请求超时，请稍后重试');
					})

			},
			/*
			 * params:无
			 * return: 无
			 * function:重置按钮事件
			 * */
			reset() {
				this.list.title = "",
					this.list.content = '',
					this.list.crtUser = ''
			},
			/*
			 * params:page:  pageSize:
			 * return: 无
			 * function:获取动态新闻的列表
			 * */
			dongtai() {
				this.$axios.post(URL.dongtai, {
						page: 1,
						pageSize: 5,
					}).then(res => {
						//console.log(res.data)
						this.tabdata = res.data.map.list
					})
					.catch(err => {
						console.log(err)
					})
			},
		},

	}
</script>

<style lang="scss">
	.main {
		background: #fff;
		margin: 0 4%;
		padding: 0 4%;
		.left {
			width: 50%;
			.el-form-item__content {
				width: 50%;
			}
			.el-form-item__label {
				padding: 0;
			}
			#nameLeft {
				text-align: left;
				padding: 0;
			}
		}
		.right {
			width: 50%;
			.el-select {
				width: 58%;
				.el-input {
					width: 100%;
				}
			}
		}
		.el-form-item {
			border-bottom: 1px solid #f1f1f1;
			margin-bottom: 0;
			.el-select {
				width: 53%;
				.el-input {
					width: 100%;
				}
			}
			.el-form-item__label {
				line-height: 50px;
			}
			.el-input {
				width: 53%;
				line-height: 50px;
				float: right;
				.el-input__inner {
					border: none;
					text-align: right;
				}
			}
		}
	}
	
	.s {
		color: #9A9A9A;
		width: 100%;
		height: 60px;
		display: flex;
		align-items: center;
		border-bottom: 1px solid #DDDDDD;
	}
	
	.xg {
		height: 580px;
		width: 1000px;
		position: fixed;
		top: 0;
		left: 250px;
		background: rgb(199, 202, 204);
	}
	
	.xga {
		width: 500px;
		margin: 0 auto;
	}
	
	.st {
		margin-left: 3%;
		color: #777777;
		font-size: 20px;
		font-weight: bold;
	}
	
	.tab {
		width: 100%;
		background: white;
	}
	
	.tabb {
		width: 50%;
		margin: 0 auto;
		padding-bottom: 20px;
	}
</style>


