	fagui: base + '/web/YsLawsController/queryAll', //政策法规
	dongtai: base + '/web/YsCreditController/queryAll', //信用动态
	newlist: base + '/web/YsNewsController/queryAll', //新闻
	swiper: base + '/web/ysPicController/queryAll', //轮播
	fuwu: base + '/web/YsServicesController/queryAll', //服务列表

	imag: base + '/someFileUpload' //上传 图片
