<!--author:王超楠
	created:2018-07-13
	effect:服务记录-->
<template>
	<div id="fuwu">
		<div class="s">
			<div class="st">
				<p>管理员列表</p>
			</div>
		</div>
		<div class="tab">
			<el-form label-width="80px" :model="list" class="tabb">
				<el-form-item label="题目">
					<el-input v-model="list.title"></el-input>
				</el-form-item>
				<el-form-item label="内容">
					<el-input v-model="list.content"> </el-input>
				</el-form-item>
				<el-form-item label="创建人">
					<el-input v-model="list.crtUser"></el-input>
				</el-form-item>
				<el-form-item label="上传图片">
					<el-upload :action="upImg" name="file" :limit="1" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="imgList" :on-success="success" :on-exceed="limitLayer" list-type="picture-card">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible" append-to-body>
						<img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				<el-button type="primary" @click="submit">添加</el-button>
				<el-button type="danger" @click="reset">重置</el-button>
			</el-form>
		</div>

		<el-table :data="arr.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%">
			<el-table-column prop="" label="图片" width="180">
				<template slot-scope="scope">
					<img :src="scope.row.picUrl" width="100" />
				</template>
			</el-table-column>
			<el-table-column prop="title" label="题目" width="180">
			</el-table-column>
			<el-table-column prop="crtUser" label="创建人" width="140">
			</el-table-column>
			<el-table-column prop="content" label="内容">
			</el-table-column>
			<el-table-column prop="crtTime" label="创建时间">
			</el-table-column>
			<el-table-column prop="" label="操作">
				<template slot-scope="scope">
					<el-button size="mini" @click="bj(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!-- 分页器 -->
		<div align="center">
			<el-pagination @current-change="current_change" :current-page="currentPage" :page-size="pagesize" layout="total, prev, pager, next, jumper" :total="arr.length">
			</el-pagination>
		</div>
		<!-- 弹框部分 -->
		<div v-if="flag" class="xg">
			<fuwu :cdz="data" class="xga" @tianjia="tianjia"></fuwu>
		</div>
	</div>
</template>

<script>
	import fuwu from './fuwu'
	import URL from '../urlConfig'
	import axios from "axios";
	export default {
		components: {
			fuwu
		},
		data() {
			return {
				pagesize: 3, //默认每页数据量              
				currentPage: 1, //当前页码                
				totalCount: 0, //默认数据总数
				flag: false,
				arr: [], //服务列表
				list: {
					title: '', //题目
					content: '', //内容
					crtUser: '', //创建人
					id: '',

				},
				upImg: URL.imag, // 'https://jsonplaceholder.typicode.com/posts/'//上传图片接口
				imgList: [],
				dialogVisible: false,
				dialogImageUrl: ''
			}
		},
		methods: {
			/*
			 * params:currentPage
			 * return: 无
			 * function:页码数量
			 * */
			current_change: function(currentPage) {
				this.currentPage = currentPage;
			},
			/*
			 * params:无
			 * return: 无
			 * function: 提交按钮
			 * */
			submit() {
				var all = false;
				for(var item in this.list) {
					if(this.list[item].length == 0) {
						all = true;
					}
				}
				if(all) {
					this.$layer.alert("请填写完整信息")
				} else {
					this.$axios.post(URL.addzheng, {
							title: this.list.title,
							content: this.list.content,
							crtUser: this.list.crtUser,
						}).then(res => {
							//console.log(res.data)
							this.$layer.alert("提交成功")
							this.fagui()
						})
						.catch(err => {
							console.log(err)
						})
				}
			},

			/*
			 * params: 无
			 * return: 无
			 * function: 重置按钮
			 * */
			reset() {
				this.list.title = "",
					this.list.content = '',
					this.list.crtUser = ''
			},
			/*
			 * params:文件对象,文件列表
			 * return: 无
			 * function: 上传文件
			 * */
			handleRemove(file, fileList) {
				var arr = [];
				fileList.forEach((item, i) => {
					arr.push({
						url: item.url
					})
				})
				this.imgList = arr;
			},
			/*
			 * params:无
			 * return: 无
			 * function: 最多上传多少图片
			 * */
			limitLayer() {
				//				layer.msg('最多上传1张图片');
				this.$layer.alert('最多上传1张图片')
			},
			/*
			 * params:file
			 * return: 无
			 * function: 提交按钮点击事件
			 * */
			handlePreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			/*
			 * params:file
			 * return: 无
			 * function: 上传图片
			 * */
			success(file) { //上传图片
				this.imgList.push({
					url: file.picUrl || ''
				})
			},
			/*
			 * params: val
			 * return: 无
			 * function:添加的方法
			 * */
			tianjia(val) {
				this.flag = val;
			},
			/*
			 * params: x,y
			 * return: 无
			 * function: 显示编辑弹框
			 * */
			bj(x, y) {
				this.flag = !this.flag;
				//console.log(x)
				this.data = y;
			},
			/*
			 * params:index.row
			 * return: 无
			 * function:删除
			 * */
			del(index, row) {
				this.$axios.post(URL.delfuwu, {
						ids: this.arr[index].id,
					}).then(res => {
						this.arr.splice(index, 1)
						this.$layer.alert("删除成功")
						this.fuwu()
					})
					.catch(err => {
						console.log(err)
					})
			},
			/*
			 * params:page: pageSize:
			 * return: 无
			 * function:服务列表
			 * */
			fuwu() {
				this.$axios.post(URL.fuwu, {
						page: 1,
						pageSize: 8,
					}).then(res => {
						//console.log(res.data)
						this.arr = res.data.map.list
					})
					.catch(err => {
						console.log(err)
					})
			},
			//			imge() {
			//				this.$axios.post(URL.imag).then(res => {
			//						console.log(res.data)
			//						
			//					})
			//					.catch(err => {
			//						console.log(err)
			//					})
			//			},
		},

		created() {
			this.fuwu();
			//this.imge()
		}
	}
</script>

<style scoped>
	.tab {
		width: 100%;
		background: white;
		height: 500px;
		padding-top: 20px;
	}
	
	.tabb {
		width: 50%;
		margin: 0 auto;
		padding-bottom: 20px;
	}
	
	.s {
		color: #9A9A9A;
		width: 100%;
		height: 60px;
		line-height: 60px;
		border-bottom: 1px solid #DDDDDD;
		border: 1px solid #DDDDDD;
	}
	
	.st {
		margin-left: 3%;
		color: #777777;
		font-size: 20px;
		font-weight: bold;
	}
	
	.xg {
		height: 550px;
		width: 1050px;
		position: fixed;
		top: 0;
		left: 250px;
		background: rgb(199, 202, 204)
	}
	
	.xga {
		width: 500px;
		margin: 0 auto;
	}
</style>


