<!--author:王超楠
	created:2018-07-13
	effect:轮播图记录-->
<template>
	<div>
		<div class="s">
			<div class="st">
				<p>党员信息管理</p>
			</div>
		</div>
		<!--<div class="tab">
			<el-form :label-position="labelPosition" label-width="80px" :model="list" class="tabb">
				<el-form-item label="创建人">
					<el-input v-model="list.crtUser" placeholder="创建人"></el-input>
				</el-form-item>
				<el-form-item label="请选择对应新闻">
					<el-dropdown>
						<el-button type="primary">
							更多新闻<i class="el-icon-arrow-down el-icon--right"></i>
						</el-button>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item v-for='(item,key) in lists' :key="key">{{item.programaName}}</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</el-form-item>
				<el-form-item label="上传图片">
					<el-upload :action="upImg" name="file" :limit="1" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="imgList" :on-success="success" :on-exceed="limitLayer" list-type="picture-card">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible" append-to-body>
						<img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				<el-button type="primary" @click="submit">添加</el-button>
				<el-button type="danger" @click="reset">重置</el-button>
			</el-form>
		</div>-->

		<el-table :data="tableData" style="width: 100%">
			<!--<el-table-column prop="" label="图片" width="180">
				<template slot-scope="scope">
					<img :src="scope.row.picUrl" width="100" />
				</template>
			</el-table-column>-->
			<el-table-column prop="auditState" label="审核状态"></el-table-column>
			<el-table-column prop="name" label="姓名"></el-table-column>
			<el-table-column prop="workUnit" label="工作单位"></el-table-column>
			<el-table-column prop="nation" label="民族"></el-table-column>
			<el-table-column prop="birthDay" label="出生日期"></el-table-column>
			<el-table-column prop="education" label="学历"></el-table-column>
			<el-table-column prop="workJobs" label="工作岗位"></el-table-column>
			<el-table-column prop="province" label="省"></el-table-column>
			<el-table-column prop="city" label="市"></el-table-column>
			<el-table-column prop="county" label="县"></el-table-column>

			<el-table-column prop="" label="操作" width="500">
				<template slot-scope="scope">
					<el-button size="mini" @click="audit(scope.$index, scope.row)">审核</el-button>
					<el-button size="mini" @click="bj(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
					<el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">查看党员信息详情</el-button>
					<el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">党关系转换详情</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!-- 分页器 -->
		<div align="center">
			<el-pagination @current-change="current_change" :current-page="currentPage" :page-size="pagesize" layout="total, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
		<!-- 弹框部分 -->
		<el-dialog :visible.sync="flag">
			<el-form :model="infoForm" class="main position01">
				<h2 class="title">基本信息表</h2>

				<div class="row">
					<el-form-item label="姓名：" class="left">
						<el-input v-model="infoForm.name" class="name-input" placeholder="王某某"></el-input>
					</el-form-item>
					<el-form-item class="right" label="性别：">
						<el-select v-model="infoForm.sex" class="gender">
							<el-option v-for="(item,index) in sexs" :key="index" :label="item.name" :value="item.value"></el-option>

						</el-select>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="民族：">
						<el-select v-model='infoForm.nation' placeholder="点击请选择" class="national">
							<el-option v-for="(item,index) in nations" :key="item.dicId" :label="item.dicName" :value="item.dicId"></el-option>

						</el-select>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="公民身份证号:">
						<el-input v-model="infoForm.idNumber" class="cardId" placeholder="13248723874273"></el-input>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="出身日期:">
						<el-date-picker v-model="infoForm.birthDay" type="date" placeholder="选择日期" class="datePicker">
						</el-date-picker>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="学历：">
						<el-select v-model="infoForm.education" placeholder="点击请选择" class="national">
							<el-option v-for="(item,index) in educations" :key="item.dicId" :label="item.dicName" :value="item.dicId"></el-option>

						</el-select>
					</el-form-item>
				</div>

			</el-form>
			<el-form class="main position02">
				<div class="row">
					<el-form-item label="人员类别：">
						<el-select v-model="infoForm.category" placeholder="点击请选择" class="national">
							<el-option label="正式党员" value="正式党员"></el-option>
							<el-option label="预备党员" value="预备党员"></el-option>
						</el-select>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="工作岗位：">
						<el-select v-model="infoForm.workJobs" placeholder="点击请选择" class="national">
							<el-option v-for="(item,index) in works" :key="index" :label="item.dicName" :value="item.dicId"></el-option>

						</el-select>
					</el-form-item>
				</div>
			</el-form>
			<el-form class="main position03">
				<div class="row">

					<el-form-item label="联系电话：">
						<el-input v-model="infoForm.phone" class="cardId" placeholder="请点击填写"></el-input>
					</el-form-item>

				</div>
				<div class="row">
					<el-form-item label="固定电话：">
						<el-input v-model="fixedPhone01" class="fixedPhone" placeholder="请点击填写"></el-input>+
						<el-input v-model="fixedPhone02" class="fixedPhone" placeholder="请点击填写"></el-input>
					</el-form-item>
				</div>
				<div class="row">

					<el-form-item label="家庭住址（具体到门牌号）:">
						<!--<v-distpicker type="mobile"></v-distpicker>-->
						<!--<area-select v-model="selected"></area-select>
					<area-cascader v-model="selected2"></area-cascader>-->
						<el-input v-model="infoForm.address" placeholder="点击填写详细地址"></el-input>
					</el-form-item>

				</div>
			</el-form>
			<el-form class="main position04">
				<div class="row">
					<el-form-item label="党籍状态：">
						<el-select v-model="infoForm.partyState" placeholder="点击请选择" class="national">
							<el-option label="正常" value="正常"></el-option>
							<el-option label="停止党籍" value="停止党籍"></el-option>
						</el-select>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="是否为失联党员：">
						<el-select v-model="infoForm.lostIn" placeholder="点击请选择" class="cnational">
							<el-option label="是" value="是"></el-option>
							<el-option label="否" value="否"></el-option>
						</el-select>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="失去联系日期:">
						<el-date-picker v-model="infoForm.lostTime" type="date" placeholder="选择日期" class="datePicker">
						</el-date-picker>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="是否为流动党员：">
						<el-select v-model="infoForm.theFlow" placeholder="点击请选择" class="cnational">
							<el-option label="是" value="是"></el-option>
							<el-option label="否" value="否"></el-option>
						</el-select>
					</el-form-item>
				</div>
				<div class="row">
					<el-form-item label="工作单位名称:">
						<el-input v-model="infoForm.workUnit" class="cardId" placeholder="请点击填写"></el-input>
					</el-form-item>
				</div>
				<el-button type="primary" @click="submit">确认修改</el-button>
			</el-form>
		</el-dialog>
	</div>
</template>

<script>
	import swipe from './swipe'
	import URL from '../urlConfig'
	import axios from '../ajax'
	import $http from 'axios'
	import * as Cookies from 'tiny-cookie'
	export default {
		components: {
			swipe
		},
		data() {
			return {
				pagesize: 3, //默认每页数据量				
				currentPage: 1, //当前页码				
				totalCount: 0, //默认数据总数
				flag: false,
				labelPosition: 'top', //表格定位
				upImg: URL.imag, //,'https://jsonplaceholder.typicode.com/posts/'//上传图片的接口
				list: {
					picUrl: '', //图片路径
					picTypeId: '', //图片分类
					crtUser: '', //创建人
				},
				swipe: [], //轮播图的数据
				tableData: [],
				imgList: [],
				dialogVisible: false,
				dialogImageUrl: '',
				lists: [],
				total: 0,
				infoForm: {
					name: '',
					sex: '',
					nation: '',
					idNumber: '',
					birthDay: '',
					education: '',
					category: '',
					workJobs: '',
					phone: '',
					fixedPhone: '',
					address: '',
					province: '',
					city: '',
					county: '',
					partyState: '',
					lostIn: '',
					lostTime: '',
					theFlow: '',
					workUnit: '',
				},
				sexs: [{
					name: '男',
					value: '男'
				}, {
					name: '女',
					value: '女'
				}],
				nations: [],
				educations: [],
				works: [],
				fixedPhone01: '',
				fixedPhone02: '',
			}
		},
		mounted() {
			this.getData();
		},
		methods: {
			/*
			 * params:index,row
			 *
			 * return: 无
			 * function:删除事件
			 * */
			audit(index, row) {
				const c = confirm('通过审核');
				if(c) {
					axios.post(URL.partyAudit, {
							ids: row.id,
							auditState: 1
						}).then(res => {

							if(res.data.code == 200) {
								this.$layer.msg("审核通过");
								this.getData();
							}
						})
						.catch(err => {
							this.$layer.msg('请求超时，请稍后重试');
						})
				}

			},
			/*
			 * params:无
			 * return: 无
			 * function:页码数量
			 * */
			getData() {
				axios.post(URL.lookDictionary, {
						currentPage: this.currentPage,
						pageSize: this.pagesize
					})
					.then(res => {
						if(res.data.code == 200) {
							this.tableData = res.data.flag.list;
							this.total = res.data.flag.totalCount;
							this.tableData.forEach((item, index) => {
								if(item.auditState) {
									item.auditState = '已审核'
								} else {
									item.auditState = '未审核'
								}
							})
						}
					})
					.catch(err => {
						this.$layer.msg('请求超时，请稍后重试');
					})
			},
			/*
			 * params:无
			 * return: 无
			 * function:页码数量
			 * */
			current_change: function(currentPage) {
				this.currentPage = currentPage;
				this.getData();
			},
			/*
			 * params: val
			 * return: 无
			 * function:添加的方法
			 * */
			tianjia(val) {
				this.flag = val;
			},
			/*
			 * params: x,y
			 * return: 无
			 * function: 显示编辑弹框
			 * */
			bj(x, y) {
				this.flag = !this.flag;
				//console.log(x)
				axios.post(URL.partyMember, {
						phone: y.phone,
					}).then(res => {

						if(res.data.code == 200) {
							this.infoForm = res.data.flag;
						}
					})
					.catch(err => {
						this.$layer.msg('请求超时，请稍后重试');
					})
			},
			/*
			 * params:index.row
			 * return: 无
			 * function:删除
			 * */
			del(index, row) {
				axios.post(URL.delParty, {
						ids: row.id,
					}).then(res => {

						this.$layer.msg("删除成功");
						this.getData();
					})
					.catch(err => {
						this.$layer.msg('请求超时，请稍后重试');
					})
			},
			/*
			 * params:文件对象,文件列表
			 * return: 无
			 * function: 上传文件
			 * */
			handleRemove(file, fileList) {
				var arr = [];
				fileList.forEach((item, i) => {
					arr.push({
						url: item.url
					})
				})
				this.imgList = arr;
			},
			/*
			 * params:无
			 * return: 无
			 * function: 最多上传多少图片
			 * */
			limitLayer() {
				//				layer.msg('最多上传1张图片');
				this.$layer.alert('最多上传1张图片')
			},
			/*
			 * params:file
			 * return: 无
			 * function: 提交按钮点击事件
			 * */
			handlePreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			/*
			 * params:file
			 * return: 无
			 * function: 上传图片
			 * */
			success(file) { //上传图片
				this.imgList.push({
					url: file.picUrl || ''
				})
			},
			/*
			 * params:无
			 * return: 无
			 * function: 提交按钮点击事件
			 * */
			submit() {

				let TOKEN = Cookies.get('ADMINTOKEN') || '';
				$http.post(URL.addDetail, this.infoForm, {
						dataType: "JSON",
						headers: {
							"Content-Type": "application/json;charsetset=UTF-8",
							"Authorization": TOKEN
						}
					})
					.then(res => {
						if(res.data.code == 200) {
							this.flag = false;
							this.$layer.msg('修改党员信息成功');
							this.getData();
						}
					})
					.catch(err => {
						this.$layer.msg('请求超时,请重试');
					})

			},
			/*
			 * params:无
			 * return: 无
			 * function: 重置按钮
			 * */
			reset() {
				this.list.title = "",
					this.list.content = '',
					this.list.crtUser = '',
					this.list.programa = '',
					this.list.programaName = ''
			},
			/*
			 * params:无
			 * return: 无
			 * function: 轮播图请求
			 * */
			swiper() {
				this.$axios.post(URL.swiper, {
						page: 1,
						pageSize: 2,
					}).then(res => {
						//console.log(res.data)
						this.arr = res.data.map.list
					})
					.catch(err => {
						console.log(err)
					})
			},

			/*
			 * params:无
			 * return: 无
			 * function: 列表请求
			 * */
			listt() {
				this.$axios.post(URL.listt, ).then(res => {
						//console.log(res.data)
						this.lists = res.data.map
					})
					.catch(err => {
						console.log(err)
					})
			},

		},
		created() {
			//this.swiper();
			//this.listt();
		}
	}
</script>
<style scoped>
	.s {
		color: #9A9A9A;
		width: 100%;
		height: 60px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #DDDDDD;
	}
	
	.st {
		margin-left: 3%;
		color: #777777;
		font-size: 20px;
		font-weight: bold;
	}
	
	.tab {
		width: 100%;
		background: white;
		height: 600px;
	}
	
	.tabb {
		width: 50%;
		margin: 0 auto;
		padding-bottom: 20px;
	}
	
	.tabb p {
		padding: 20px 0 5px;
		color: #9B9AA8;
	}
	
	.xg {
		height: 600px;
		width: 1050px;
		position: fixed;
		top: 0;
		left: 250px;
		background: rgb(199, 202, 204)
	}
	
	.xga {
		width: 500px;
		margin: 0 auto;
	}
</style>


